.button {
  display: inline-block;
  min-width: 100px;
  padding: 17px 25px;
  line-height: 1.1em;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-decoration: none;
  font-size: 0.8em;
  transition: 0.1s ease-in;
  border: 2px solid #F1173A; }
  .button:hover {
    filter: brightness(1.1); }

.button--solid {
  background-color: #F1173A;
  color: white; }

.button--outline {
  background-color: white;
  color: #F1173A; }
